body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  background-color: rgb(255, 255, 255);
  color: rgb(14, 14, 14);
  margin: 0 auto;
  text-align: center;
}

.heading {
  background-color: rgb(143, 143, 143);
  padding: 15px;
  color: white;
  font-size: calc(10px + 2vmin);
}

h1 {
  margin: 0 auto;
}

form {
  display: -webkit-flex;
  display: flex;
  margin: calc(1rem + 1rem) calc(2rem + 1rem) 0 calc(2rem + 1rem);
}

input[type="text"] {
  -webkit-flex: 8 1;
          flex: 8 1;
  padding: 5px;
  font-size: calc(5px + 2vmin);
}

button[type="submit"] {
  -webkit-flex: 2 1;
          flex: 2 1;
  font-size: calc(5px + 2vmin);
  background-color: rgb(143, 143, 143);
  color: white;
  border: none;
}

.list {
  margin: 20px 0 50px 0;
  font-size: calc(5px + 2vmin);
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}

.card {
  margin: 0 auto;
  padding: 15px;
  border: none;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.desc {
  width: 150px;
  height: 200px;
  padding: 10px;
}

h2 {
  font-size: 15px;
}

p {
  font-size: 10px;
}
